<script>
import { TabPane } from 'view-design'
import ExampleCarousel from './exampleCarousel'
export default {
  components: {
    TabPane,
    ExampleCarousel
  },
  props: {
    item: Object
  },
  computed: {
    // 创建 tab label元素
    label () {
      return (h) => {
        return h('div', [
          h('img', {
            attrs: {
              src: this.item.icon
            },
            style: {
              display: 'block',
              width: '32px',
              height: '32px',
              margin: '0 auto'
            }
          }),
          h('span', {
            style: {
              display: 'block',
              marginTop: '16px',
              color: '#000',
              fontFamily: 'PingFang SC',
              fontStyle: 'normal',
              fontWeight: '500',
              fontSize: '18px',
              lineHeight: '25px',
              textAlign: 'center'
            }
          }, this.item.label)
        ])
      }
    }
  },
  render (h) {
    return h(TabPane, {
      attrs: {
        label: this.label,
        name: this.item.name
      }
    }, [h(ExampleCarousel, {
      // attrs: {
      //   class: 'custom-carousel'
      // },
      props: {
        childrenDatas: this.item.childrenContxt
      }
    })])
  }
}
</script>
